.result-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  padding: 30px;
}

.result-title {
  align-self: flex-start;
  margin-bottom: 1rem;
  font-weight: 700;
  color: #333;
  font-family: 'Bw Modelica Bold', sans-serif;
  font-size: 32px;
  line-height: 38px;
  text-transform: capitalize;
  text-align: left;
}

.result-grid {
  /* flex: 1; */
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  /* grid-template-columns: 1fr 1fr 1fr; */
  /* grid-gap: 10px; */
  margin-bottom: 30px;
  width: 100%;
  /* flex: 1;
  display: flex;
  flex-direction: column; */
}

.result-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-family: 'Bw Modelica Bold', sans-serif;
}

.result-item-title {
  font-size: 18px;
  font-weight: 600;
  color: #333;
  font-family: 'Bw Modelica Regular', sans-serif;
  font-style: normal;
  line-height: 22px;
}

.result-item-value {
  font-size: 24px;
  font-weight: 400;
  color: #000000;
  font-weight: 700;
  font-size: 32px;
  font-family: 'Bw Modelica Bold', sans-serif;
}

.tradingview-container {
  width: 100%;
  flex: 1;
  margin-top: 1rem;
}

.tradingview-title {
  font-weight: 600;
  margin-bottom: 10px;
  color: #333;
  font-family: 'Bw Modelica Bold', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-transform: capitalize;
}

.content-wrapper {
  display: flex;
  flex: 1;
  flex-direction: column;
  padding-right: 1rem;
  margin-right: 1rem;
  /* align-items: flex-start; */
}

.cover-call-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  border-radius: 4px;
}

.title-and-content-wrapper {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 1rem;
  border-radius: 4px;
  margin-right: 1rem;
}

.progress-bar-wrapper {
  margin-top: 2rem;
  margin-bottom: 2rem;
}
