/* styles.css */
.input-wrapper {
  position: relative;
  margin-bottom: 1.5rem;
}

input {
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 0.875rem;
  width: 100%;
  box-sizing: border-box;
  transition: 0.2s ease all;
}

input:focus {
  border-color: #0070f3;
  box-shadow: 0 0 0 1px #0070f3;
  outline: none;
}

.floating-label {
  position: absolute;
  pointer-events: none;
  left: 0.5rem;
  top: 0.1rem;
  transition: 0.2s ease all;
  color: #999;
  font-size: 0.675rem;
}

.floating-label.active {
  /* top: -20px; */
  color: #007bff;
  -webkit-transform: translateY(-1.5rem);
          transform: translateY(-1.5rem);
  font-size: 0.75rem;
}

.input-error {
  border-color: #f44336;
  box-shadow: 0 0 0 1px #f44336;
}

.error-message {
  font-size: 0.75rem;
  color: #f44336;
  margin-top: 0.25rem;
}

.error {
  color: #ff0000;
}

input:focus ~ .floating-label {
  -webkit-transform: translateY(-1.5rem);
          transform: translateY(-1.5rem);
  font-size: 0.75rem;
  color: #0070f3;
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 1rem;
  gap: 1rem;
  max-width: 90%;
  margin: 0 auto;
}

.form-grid > div {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: calc(25% - 1rem);
}

.button-container {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}
.main-title {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 2rem;
}

.tradingview-widget-container {
  width: 100%;
  height: calc(100vh - 60px);
  min-height: 600px;
  overflow: hidden;
}

#tradingview_215b4 {
  height: 100%;
}

.tradingview-widget-title {
}

.result-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  padding: 30px;
}

.result-title {
  align-self: flex-start;
  margin-bottom: 1rem;
  font-weight: 700;
  color: #333;
  font-family: 'Bw Modelica Bold', sans-serif;
  font-size: 32px;
  line-height: 38px;
  text-transform: capitalize;
  text-align: left;
}

.result-grid {
  /* flex: 1; */
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
  /* grid-template-columns: 1fr 1fr 1fr; */
  /* grid-gap: 10px; */
  margin-bottom: 30px;
  width: 100%;
  /* flex: 1;
  display: flex;
  flex-direction: column; */
}

.result-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-family: 'Bw Modelica Bold', sans-serif;
}

.result-item-title {
  font-size: 18px;
  font-weight: 600;
  color: #333;
  font-family: 'Bw Modelica Regular', sans-serif;
  font-style: normal;
  line-height: 22px;
}

.result-item-value {
  font-size: 24px;
  font-weight: 400;
  color: #000000;
  font-weight: 700;
  font-size: 32px;
  font-family: 'Bw Modelica Bold', sans-serif;
}

.tradingview-container {
  width: 100%;
  flex: 1 1;
  margin-top: 1rem;
}

.tradingview-title {
  font-weight: 600;
  margin-bottom: 10px;
  color: #333;
  font-family: 'Bw Modelica Bold', sans-serif;
  font-style: normal;
