.content-wrapper {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  padding-right: 1rem;
  margin-right: 1rem;
  /* align-items: flex-start; */
}

.cover-call-container {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  border-radius: 4px;
}

.title-and-content-wrapper {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  padding: 1rem;
  border-radius: 4px;
  margin-right: 1rem;
}

.progress-bar-wrapper {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

:root {
  --color-white: #fff;
  --color-black: #333;
  --total-black: #000;
  --color-gray: #75787b;
  --color-gray-light: #bbb;
  --color-gray-disabled: #e8e8e8;
  --color-green: #53a318;
  --color-green-dark: #383;
  --font-size-small: 0.75rem;
  --font-size-default: 0.875rem;
}

* {
  box-sizing: border-box;
}

body {
  margin: 2rem;
  font-family: 'Open Sans', sans-serif;
  color: #333;
  color: var(--color-black);
}

h2 {
  color: #75787b;
  color: var(--color-gray);
  font-size: 0.75rem;
  font-size: var(--font-size-small);
  line-height: 1.5;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 3px;
}
section {
  margin-bottom: 2rem;
}

.progress-bar {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: 0 0 1rem 0;
}

.progress-bar li {
  flex: 2 1;
  position: relative;
  padding: 0 0 14px 0;
  font-size: 0.875rem;
  font-size: var(--font-size-default);
  line-height: 1.5;
  color: #53a318;
  color: var(--color-green);
  font-weight: 600;
  white-space: nowrap;
  overflow: visible;
  min-width: 0;
  text-align: center;
  border-bottom: 2px solid #000;
  border-bottom: 2px solid var(--total-black);
}
.progress-bar li:first-child,
.progress-bar li:last-child {
  flex: 1 1;
}
.progress-bar li:last-child {
  text-align: right;
}
