  transition: all 0.2s ease-in-out;
} */

.progress-bar li:before {
  content: '';
  display: block;
  width: 3px; /* Increased width */
  height: 30.5px; /* Reduced height */
  background-color: #000;
  background-color: var(--total-black);
  border-radius: 0; /* Removed border-radius */
  position: absolute;
  left: calc(50% - 6px); /* Updated left property */
  bottom: -15px; /* Updated bottom property */
  z-index: 3;
  transition: all 0.2s ease-in-out;
}

.progress-bar li:first-child:before {
  left: 0;
}
/* .progress-bar li:last-child:before {
  right: 0;
  left: auto;
} */
.progress-bar li:last-child {
  position: relative;
}
.progress-bar li:last-child::after,
.progress-bar li:last-child:before {
  content: '';
  background: #000;
  background: var(--total-black);
  position: absolute;
  height: 3px;
  width: 15px;
}
.progress-bar li:last-child:before {
  bottom: -7px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  left: 10px;
}
.progress-bar li:last-child:after {
  right: -1px;
  top: 29px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.progress-bar span {
  transition: opacity 0.3s ease-in-out;
}
.progress-bar li:not(.is-active) span {
  opacity: 0;
}
.progress-bar .is-complete:not(:first-child):after,
.progress-bar .is-active:not(:first-child):after {
  content: '';
  display: block;
  width: 100%;
  position: absolute;
  bottom: -2px;
  left: -50%;
  z-index: 2;
  border-bottom: 2px solid #000;
  border-bottom: 2px solid var(--total-black);
}
.progress-bar li:last-child span {
  width: 200%;
  display: inline-block;
  position: absolute;
  left: -100%;
}

.progress-bar .is-complete:last-child:after,
.progress-bar .is-active:last-child:after {
  width: 200%;
  left: -100%;
}

.progress-bar .is-complete:before {
  background-color: #000;
  background-color: var(--total-black);
}
.progress-bar .is-active:before,
.progress-bar li:hover:before,
.progress-bar .is-hovered:before {
  background-color: #000;
  background-color: var(--total-black);
  border-color: #000;
  border-color: var(--total-black);
}
.progress-bar li:hover:before,
.progress-bar .is-hovered:before {
  -webkit-transform: scale(1.33);
          transform: scale(1.33);
}

.progress-bar li:hover span,
.progress-bar li.is-hovered span {
  opacity: 1;
}

.progress-bar:hover li:not(:hover) span {
  opacity: 0;
}

.x-ray .progress-bar,
.x-ray .progress-bar li {
  border: 1px dashed red;
}

.progress-bar .has-changes {
  opacity: 1 !important;
}
.progress-bar .has-changes:before {
  content: '';
  display: block;
  width: 8px;
  height: 8px;
  position: absolute;
  left: calc(50% - 4px);
  bottom: -20px;
  background-image: url('data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%208%208%22%3E%3Cpath%20fill%3D%22%23ed1c24%22%20d%3D%22M4%200l4%208H0z%22%2F%3E%3C%2Fsvg%3E');
}

