/* styles.css */
.input-wrapper {
  position: relative;
  margin-bottom: 1.5rem;
}

input {
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 0.875rem;
  width: 100%;
  box-sizing: border-box;
  transition: 0.2s ease all;
}

input:focus {
  border-color: #0070f3;
  box-shadow: 0 0 0 1px #0070f3;
  outline: none;
}

.floating-label {
  position: absolute;
  pointer-events: none;
  left: 0.5rem;
  top: 0.1rem;
  transition: 0.2s ease all;
  color: #999;
  font-size: 0.675rem;
}

.floating-label.active {
  /* top: -20px; */
  color: #007bff;
  transform: translateY(-1.5rem);
  font-size: 0.75rem;
}

.input-error {
  border-color: #f44336;
  box-shadow: 0 0 0 1px #f44336;
}

.error-message {
  font-size: 0.75rem;
  color: #f44336;
  margin-top: 0.25rem;
}

.error {
  color: #ff0000;
}

input:focus ~ .floating-label {
  transform: translateY(-1.5rem);
  font-size: 0.75rem;
  color: #0070f3;
}
